#apppage {
  background: #F7F7F7;
  }

#line {
  height: 1.5px;
  background: #4F4F50;
}

.navBrand {
  font-family: sans-serif;
  font-weight: bold;
  letter-spacing: 0.04em;
  cursor: pointer;
  border-radius: 10px;
  font-size: 36px;
  padding: 5px;
}

.navLink {
  font-family: sans-serif;
  font-size: 18px;
  font-weight: bold;
  letter-spacing: 0.07em;
  cursor: pointer;
  border-radius: 10px;
}

.navLink:hover, .navBrand:hover {
  background: #222222;
}


.header {
  font-family: Montserrat;
  font-style: normal;
  font-size: 48px;
  line-height: 59px;

  color: #A2B689;
}

.sectionHeader {
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 38px;
  line-height: 44px;

  color: #A2B689;
}

.textHeader {
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
}

.credits {
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 29px;
  letter-spacing: 0.07em;

  color: #A2B689;
}

  .textDescription {
    font-family: Montserrat;
    font-style: italic;
    font-weight: 250;
    font-size: 18px;
    line-height: 26px;

    color: #888888;
  }

  .uploadBtn {
    border: 3px solid #A2B689;
    box-sizing: border-box;
    /* box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); */
    border-radius: 50px;
    width: 20vw;
    height: 5vh;
    background: transparent;
  }

  .err {
    font-family: Montserrat;
    font-weight: 250;
    font-size: 18px;
    line-height: 22px;

    color: red;
  }

  .verticalLine {
    position: absolute;
    right: 3%;
    /* padding-right: 15px; */
    /* margin-left: 15px; */
    height: 82%;
    background: #F7F7F7;
    z-index: 50;
    border-left: 3px solid grey;
    opacity: 0.8;
    width: 93%;
  }

